.BreadTop {
  width: 80%;
  height: 50px;
  border-radius:  20% 20% 0 0/ 100% 100% 0 0;
  background-color: #da910a;
  display: flex;
  flex-wrap: wrap;
  flex-flow: row;
  margin: 0 auto;
}

.BreadBottom {
  width: 80%;
  height: 40px;
  border-radius: 0 0 20% 20%/ 0 0 100% 100%;
  background-color: #da910a;
  display: flex;
  margin: 0 auto;
}

.Seed1,
.Seed2 {
  flex-grow: 1;
  position: relative;
}

.Seed1:after,
.Seed1:before,
.Seed2:after,
.Seed2:before {
  content: '';
  /* width: 28px;
  height: 10px; */
  width: 20%;
  height: 10%;
  display: block;
  position: absolute;
  background-color: yellow;
  border-radius: 10px 50%
}

.Seed1:after {
  top: 39%;
  right: 10%;
  transform: rotate(40deg);
}

.Seed1:before {
  top: 30%;
  left: 10%;
  transform: rotate(140deg);
}

.Seed2:after {
  top: 24%;
  right: 20%;
  transform: rotate(140deg);
}

.Seed2:before {
  transform: rotate(40deg);
  top: 31%;
  left: 10%;
}

.Meat,.Salad,.Cheese,.Bacon, .Beef, .ShrededChicken {
  margin: 5px auto;
}

.Meat {
  width: 80%;
  height: 18px;
  border-radius: 8px/50%;
  background-color: brown;
}

.Cheese {
  width: 85%;
  height: 10px;
  border-radius: 5px/50%;
  background-color: yellow;
}

.Bacon {
  width: 85%;
  height: 10px;
  border-radius: 10px;
  background-color: rgb(219, 104, 28);
}

.Salad {
  width: 80%;
  height: 18px;
  border-radius: 10px/50%;
  background-color: rgb(98, 253, 98);
}

.Beef {
  width: 80%;
  height: 20px;
  border-radius: 10px/50%;
  background-color: rgb(92, 16, 16);
}

.ShrededChicken {
  width: 85%;
  height: 8px;
  border-radius: 3px/50%;
  background-color: rgb(238, 153, 84);
}