.BuildControl {
  width: 80%;
  margin: 10px auto;
  box-sizing: border-box; 
  /* align-items: center; */
  /* justify-content: space-between; */
}

.BuildControl:not(:last-child) {
  margin-right: 5px;
}

.Label {
  display: block;
  font-size: 9px;
}

.btns_wrapper {
  display: flex;
  flex-flow: row;
}

.ctrl_btn {
  width: 8px;
  height: 14px;
  border-radius: 50%;
  border-width: 1px;
  border-style: solid;
  background: transparent;
  display: block;
  text-align: center;
}

.More {
  border-color: green;
}

.Less {
  border-color: #ffa600;
  /* margin-right: 10px; */
}