/* Desktop View */

.Plate {
  background: #cccccc;
  box-sizing: border-box;
  height: 25px;
  width: 30%;
  margin: 0 auto 10px;
  border-radius: 0 0 50% 50%/ 0 0 100% 100%;
  border-top: 1px solid #2a2a2a;
  position: relative;
  z-index: 100;
}

.Shadow {
  /* border: 1px solid black; */
  height: 15px;
  width: 200px;
  background: rgba(0, 0, 0, .35);
  margin: 0 auto;
  border-radius: 50%;
  transform: translateY(-18px) rotateY(0deg);
  position: relative;
  z-index: 50;
}


/* Mobile View */
@media (max-width: 781px) {
  .Plate {
    width: 50vw;
  }
}