.SideDrawer {
  width: 70%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: white;
  z-index: 600;
  padding: 20px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, .125);
  transform: translateX(-100vh);
  transition: all .25s ease-out;
}

.SideDrawer ul {
  flex-flow: column;
}

.SideDrawer ul li {
  border-width: 1px;
}
.SideDrawer ul li:not(:last-child) {
  margin-bottom: 15px;
}

.Logo {
  height: 10%;
}