.Button {
  color: white;
  padding: 5px 10px;
  font-family: sans-serif;
  font-size: large;
  border-radius: 5px;
  /* box-shadow: 0 2px 1px rgba(0, 0, 0, .12); */
  cursor: pointer;
  outline: none;
}

.Danger {
  background-color: rgb(253, 94, 94);
}

.Success {
  background-color: rgb(31, 190, 127);
}